<template>
<div>
<el-row :gutter="10">
  <el-col :xs="24" :sm="12" :md="6" ><div class="grid-content circlelogo" style="background:#A1D3D7;">
       <p class="shuju"><span>40</span><span>人</span></p>
       <p>从业人员</p>
      </div></el-col>
  <el-col :xs="24" :sm="12" :md="6"><div class="grid-content circlelogo" style="background:#7476C9">
      <p class="shuju"><span>40</span><span>辆</span></p>
       <p>本日送车数</p>
      </div></el-col>
  <el-col :xs="24" :sm="12" :md="6"><div class="grid-content circlelogo" >
      <p class="shuju"><span>40</span><span>辆</span></p>
       <p>本日取车数</p>
      </div></el-col>
  <el-col :xs="24" :sm="12" :md="6"><div class="grid-content circlelogo" style="background:#AB72B3">
      <p class="shuju"><span>40</span><span>辆</span></p>
       <p>本日业务数</p>
      </div></el-col>
</el-row>
<div style="margin-left:25%; margin-top:50px;">
    <echarts></echarts>
</div>
</div>
</template>
<script>
import echarts from "./echarts"
export default {
    components:{
        echarts
    }
}
</script>
<style  scoped>
    .el-col {
    border-radius: 4px;
    margin-top: 10px;
    
    
  }
 p{
     margin: 0;
     padding: 0;
     color:#fff;
     
 }
 p.shuju{
     margin-top:30px;
    
 }
 p span{
     color:#fff;
     font-size: 20px;
 }
  .circlelogo{
    width: 125px;
    height: 125px;
    border-radius: 50%;
    background: #ccc;
    margin:0 auto;
    overflow: hidden;
   
  }

</style>


