<template>
    <div class="index">
       <el-container>
  <el-header>
         <div class="title"> 汽修业管理系统</div>
         <ul>
           <li @click="quit">退出</li>
           <li>帮助</li>
           <li @click="home">首页</li>
         </ul>
  </el-header>
  <el-container >
    <el-aside  width="200px" >
         <div style="width:200px;height:200px;background:#326EC7;overflow:hidden">
              <div class="piclogo" style="overflow:hidden"><img :src="image" alt=""></div>
              <p class="name"><span style="color:#fff;font-size: 18px;">巴达利汽修厂</span> </p>
              <p class="admin">管理员</p>
        </div>
        <el-menu
        :style="height"
      default-active="2"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#09407a"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-submenu index="1">
        <template slot="title">
           <span style="margin-right:20%">
             <i class="el-icon-odometer"></i>
          <span>承接车辆</span>
            </span> 
        </template>
          <el-menu-item index="1-4-1" ><span style="margin-left:20%" @click="carinfo">车辆信息登记</span> </el-menu-item>
          <el-menu-item index="1-4-2" ><span style="margin-left:20%" @click="connect">车辆交接登记</span></el-menu-item>
          <el-menu-item index="1-4-3"><span style="margin-left:20%" @click="car">车辆业务登记</span></el-menu-item> 
          <el-menu-item index="1-4-4"><span style="margin-left:20%" @click="accept">车辆取车登记</span></el-menu-item> 
          <el-menu-item index="1-4-5"><span style="margin-left:20%" @click="accident">事故车辆附加登记</span></el-menu-item> 
          <el-menu-item index="1-4-6"><span style="margin-left:20%" @click="dubious">可疑车辆附加登记</span></el-menu-item> 
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
             <span style="margin-right:20%">
          <i class="el-icon-s-unfold"></i>
          <span>数据上报</span>
             </span>
        </template>
          <el-menu-item index="2-4-1">选项1</el-menu-item>
          <el-menu-item index="2-4-2">选项2</el-menu-item>
          <el-menu-item index="2-4-3">选项3</el-menu-item> 
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
             <span style="margin-right:20%">
          <i class="el-icon-upload"></i>
          <span>通信管理</span>
             </span>
        </template>
          <el-menu-item index="3-4-1">选项1</el-menu-item>
          <el-menu-item index="3-4-2">选项2</el-menu-item>
          <el-menu-item index="3-4-3">选项3</el-menu-item> 
      </el-submenu>
      <el-submenu index="4">
        <template slot="title">
             <span style="margin-right:20%">
          <i class="el-icon-s-tools"></i>
          <span>系统管理</span>
             </span>
        </template>
          <el-menu-item index="4-4-1">企业信息管理</el-menu-item>
          <el-menu-item index="4-4-2" @click="employee">从业人员</el-menu-item>
        
      </el-submenu>
    </el-menu>
    </el-aside>
    <el-container>
  
       <el-main  :style="height1">
            <router-view></router-view>
      </el-main> 
    
      
      <el-footer>Footer</el-footer>
    </el-container>
  </el-container>
</el-container> 
    </div>
</template>
<script>
export default {
    data(){
        return{
            image:"http://111.21.51.70:8089/jxy_data/image/"+JSON.parse(localStorage.getItem('data')).photo,
            height:{
                width:"200px",
                height:"",
                background:"#09407a",
            },
            height1:{
              height:"",
              overflow: scroll,
              
            } 
        }
    },
    mounted() {
       this.hh(),
       this.mainheight()
    },
    methods:{
      employee(){
        this.$router.push("/employee")
      },
      quit(){
        localStorage.clear()
        this.$router.push("/login")
      },
      home(){
          this.$router.push("/home")
      },
        carinfo(){
          this.$router.push("/carinfo")
        },
        connect(){
          this.$router.push("/connect")
        },
        car(){
          this.$router.push("/car")
        },
        accept(){
          this.$router.push("/accept")
        },
        accident(){
          this.$router.push("/accident")
        },
        dubious(){
          this.$router.push("/dubious")
        },
        hh(){
            this.height.height=window.innerHeight-260+'px';
        },
        mainheight(){
          this.height1.height=window.innerHeight-120+'px'
        },
        handleOpen(key, keyPath) {
        // console.log(key, keyPath);
      },
        handleClose(key, keyPath) {
        // console.log(key, keyPath);
      }
    },
}
</script>
<style  scope>
  .index .el-header, .el-footer {
    background-color: #163258;
    color: #333;
    /* text-align: center; */
    line-height: 60px;
  }
 .index .el-aside {
    background-color:red; 
    color: #333;
    text-align: center;
    overflow: hidden;
  }
  .index .el-main {
    background-color: #fff;
    color: #333;
    text-align: center;
    position: relative;
    /* line-height: 160px; */
  }
  body > .el-container {
    margin-bottom: 40px;
  }
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
.index  el-menu-item{
      width: 200px;
  }
 .index header.el-header{
  font-size: 24px;
  font-family: 微软雅黑,serif;
    color: #f1f1f1;
}
 .index header.el-header  .title{
    float: left; 
    margin: 0;
    margin-left:5%;
     letter-spacing:5px;
}
 .index header.el-header ul li{
   float: right;
   margin-right: 30px;
  list-style:none;
  font-size: 18px;
  cursor: pointer;
}
 .index header.el-header ul{
  float: right;
  margin: 0;
}
   .index .piclogo{
      width:80px;
      height:80px;
      border: 5px solid #ccc;
      border-radius:50%;
      margin: 20px auto;
  }
  .index .name,.index .admin{
      height: 30px;
      margin: 0;
      color: #fff;
      border: 0;
      width: 200px;
      text-align: center;
  }
  .name>span{
    color: #fff;
    font-size: 18px;
  }
</style>



