<style scope>
body{
    background: #f1eddd;
}
   .imgBg1{
    width: 318px;
    height: 240px;
   position:absolute;
    top:0;
    left:0;
}
   .name{
    border:1px solid #a9a9a9;
    width:350px;
    height:34px;
    text-align:left;
    padding:5px;
}
   .pwd{
    border:1px solid #a9a9a9;width:350px;height:34px;text-align:left;padding:5px;margin-top: 40px;line-height:30px
}
.name  span{
    font-size:24px;color: #425E98;vertical-align: middle
}
 .pwd  span{
    font-size:24px;color: #425E98;vertical-align: middle
}
#login-form{
    margin-top: 110px;
}
#login-form div input{
    border:0;outline:none;width:300px;font-size:18px;vertical-align: middle;line-height: 34px
}
.window-body{
    margin:auto;
    width:1400px;
    height: 650px;
    margin-top: 140px;
    display: flex;
    position: relative;
    z-index: 99;
}
.bottom_right_img{
    position: absolute;
    width: 320px;
    height:160px;
    margin-top: -145px;
    margin-left: 1598px;
    bottom:0;
    right: 0;
}
.window-body-left{
    background-color: #f8ffff;
    width:35%;
    height: 650px;
    align-content: center;
    overflow: hidden;
    float: left;
  }
   .window-body-right{
    width: 910px;
    height: 650px;
    background-color: #152853;
    float: right;
}
.right_body_div{
    margin-top: 35px;
    margin-left: 30px;
    width: 805px;
    height: 234px;
}
.td1{
    color: #f1f1f1;
    margin-left: 25px;
    align-content: center;
    font-family: 微软雅黑,serif;
    font-size: 20px;
    padding-bottom: 10px;
    padding-top:10px;
    padding-left: 86px;
}
.right_message{
    margin-top: 75px;
    margin-left: 86px;
    font-size: 30px;
    text-align: left;
}
.right_img{
    margin-top: 55px;
    margin-left: 265.5px;
    position: absolute;
    left: 35%;
    bottom: 20px;
}
.button_log{
    margin-top: 20px; 
    width:235px ; 
    height:50px ; 
    font-size: 24px; 
    color: #f1f1f1;
    outline: none;cursor: pointer;
    position:relative;	
    z-index:100;	  
    border-radius: 15px;  
	 /* behavior: url(./resource/layui/css/ie-css3.htc); */
	 background:rgb(138,43,226);
}
.title{
    border: 100px;
    overflow: hidden;
    margin-top: 70px;
}
.input{
    height: 34px;
    width: 350px;
    line-height: 34px;
}
.rp{
    margin-top: 10px;
    margin-left: 220px;
}
.reg{
    margin-top: 10px;
    margin-left: 10px;
}
</style>
<template>
    <div>
       <div class="login">
          <img src="@/assets/imgBg1.png" class="imgBg1"> 
       </div>
        <div class="window-body" style="overflow: auto;">
            <div class="window-body-left" align="center">
                <div class="title">
                    <h1 style="color: dodgerblue">欢迎登录汽修业治安管理系统</h1>
                </div>
                <form id="login-form" method="POST" name="form1">
                    <div class="name" >
                        <span class="iconfont icon-yonghuming" ></span>
                        <input type="text" class="input"  id="username"  v-model="username">
                    </div>
                    <div class="pwd" >
                        <span class="iconfont icon-yonghuming1" ></span>
                        <input type="password" class="input"  id="password"  v-model="password">
                    </div>
                    <a class="rp" href="showLogin">忘记密码?</a>
                    <a class="reg" href="#" @click.prevent="regit">注册</a>
                    <div> <input class="button_log" type="button" value="登录"  id="bt_login" @click="login"></div>
                </form>
            </div>
            <div class="window-body-right">
                <p class="right_message" style="color: #f1f1f1"><span class="iconfont icon-horn" style="font-size:30px;color: #fff;margin-right: 15px"></span><span>信息通报</span></p>
                       <scoll></scoll>
            <img class="right_img" src="@/assets/img.png" width="346px" height="150px"> 
            </div>
        </div>
    <img class="bottom_right_img" src="@/assets/imgBg2.png" style=" z-index:-1" >
    </div>
</template>
<script>
import scoll from './scoll'
import axios from "axios"
export default {
    data() {
        return {
            username:"",
            password:""
        }
    },
    methods: {
        regit(){
            this.$router.push("/regit")
        },
        login(){
            let that =this;
            axios({
                 method: 'post',
                 url: process.env.API_HOST+'/qy/Login',
                  headers: {
                     'Content-Type':'application/json;charset=UTF-8'
                         },
                 data:JSON.stringify({
                      username: that.username,
                      password: that.password,
                 })
                 }).then((res)=>{
                      localStorage.setItem("data",JSON.stringify(res.data.data))
                      
                      if(res.data.code==200){
                          that.$message({
                          message: '登录成功',
                          type: 'success'
                          });
                          that.$router.push("/index")
                      }else if(res.data.code==401){
                          that.$message({
                         message:'用户名或密码错误',
                         type: 'warning'
                          });
                      }else{
                           that.$message({
                          message:'登录失败',
                          type: 'error'
                          });
                      }
                   
                 }).catch(function (error) {
                    console.log(error);
                    that.$message({
                                message:'登录失败',
                                type: 'error'
                                });
        });
        }
    },
   components:{
       scoll
   }
}
</script>



