<template>
  <div class="regist">
    <div class="grid-content">企业注册</div>
    <div>
      <el-row style="margin-left:15%;margin-top:100px;">
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>企业分类：</td>
                <td><input type="text" placeholder="请输入" v-model="companyType"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>行政区划：</td>
                <td><input type="text" placeholder="请输入" v-model="register"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>企业名称：</td>
                <td><input type="text" placeholder="请输入" v-model="cname"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>企业地址：</td>
                <td><input type="text" placeholder="请输入" v-model="caddress"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>联系电话：</td>
                <td><input type="text" placeholder="请输入" v-model="contway"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>传真：</td>
                <td><input type="text" placeholder="请输入" v-model="fax"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>邮箱：</td>
                <td><input type="text" placeholder="请输入" v-model="email"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>邮政编码：</td>
                <td><input type="text" placeholder="请输入" v-model="postcode"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>开业日期：</td>
                <td>
                  <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:32px;"
                    id="time">
                    <el-date-picker v-model="openDate" type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
                      size="mini" style="width:150px;">
                    </el-date-picker>
                  </div>

                </td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>经济类型：</td>
                <td><select name="" id="" v-model="ecotype">
                    <option value="">默认</option>
                    <option :value="item.code" v-for="item of ecotypelist" :key="item.id">{{item.typeName}}</option>
                  </select></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>法人代码：</td>
                <td><input type="text" placeholder="请输入" v-model="legPersonCode"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>法定代表人：</td>
                <td><input type="text" placeholder="请输入" v-model="legPerson"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>法定代表人身份证号码：</td>
                <td><input type="text" placeholder="请输入" v-model="legPersonIdcode"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>治安负责人：</td>
                <td><input type="text" placeholder="请输入" v-model="defPrincipal"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>保卫部门电话：</td>
                <td><input type="text" placeholder="请输入" v-model="defDepartContway"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>经营范围：</td>
                <td><input type="text" placeholder="请输入" v-model="businessScope"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>注册资本：</td>
                <td><input type="text" placeholder="请输入" v-model="registeredCapital"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>占地面积：</td>
                <td><input type="text" placeholder="请输入" v-model="floorSpace"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>治安管理机构：</td>
                <td><input type="text" placeholder="请输入" v-model="agency"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>治安管理机构名称：</td>
                <td><input type="text" placeholder="请输入" v-model="agencyName"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>特行备案编号：</td>
                <td><input type="text" placeholder="请输入" v-model="specRecordCode"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>特行备案机构：</td>
                <td><input type="text" placeholder="请输入" v-model="specRecordAgency"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>特行备案机构名称：</td>
                <td><input type="text" placeholder="请输入" v-model="specRecordAname"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>行业许可证号：</td>
                <td><input type="text" placeholder="请输入" v-model="induLicenceCode"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>行业许可证发证机构名称：</td>
                <td><input type="text" placeholder="请输入" v-model="induLicenceAgency"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>企业统一社会信用代码：</td>
                <td><input type="text" placeholder="请输入" v-model="unisocialCreditCode"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>统一社会信用代码发证机构名称：</td>
                <td><input type="text" placeholder="请输入" v-model="unisocialLicenceAgency"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>营业执照编号：</td>
                <td><input type="text" placeholder="请输入" v-model="busLicenceCode"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>营业执照发证机构名称：</td>
                <td><input type="text" placeholder="请输入" v-model="busLicenceAgency"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>税务登记证编号：</td>
                <td><input type="text" placeholder="请输入" v-model="taxLicenceCode"></td>
              </tr>
            </table>
          </div>
        </el-col>
        <el-col :md="24">
          <div>
            <table>
              <tr>
                <td><span style="color:red">*</span>税务登记证发证机构名称：</td>
                <td><input type="text" placeholder="请输入" v-model="taxLicenceAgency"></td>
              </tr>
            </table>
          </div>
        </el-col>
      </el-row>
      <div style="margin:20px 0 20px 5%">
        <el-row>
          <el-button type="primary" @click="define">确定</el-button>
          <el-button type="info" @click="reset">重置</el-button>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from "axios"
  export default {
    data() {
      return {
        openDate: "",
        companyType: "",
        register: '',
        cname: "",
        caddress: "",
        contway: '',
        fax: "",
        email: "",
        postcode: "",
        ecotype: "",
        legPersonCode: "",
        legPerson: '',
        legPersonIdcode: '',
        defPrincipal: "",
        defDepartContway: "",
        businessScope: "",
        registeredCapital: "",
        floorSpace: '',
        agency: '',
        agencyName: '',
        specRecordCode: '',
        specRecordAgency: "",
        specRecordAname: '',
        induLicenceCode: '',
        induLicenceAgency: '',
        unisocialCreditCode: '',
        unisocialLicenceAgency: '',
        busLicenceCode: "",
        busLicenceAgency: '',
        taxLicenceCode: '',
        taxLicenceAgency: '',
        ecotypelist: ''

      }
    },
    methods: {
      reset() {
        this.openDate = "",
          this.companyType = "",
          this.register = '',
          this.cname = "",
          this.caddress = "",
          this.contway = '',
          this.fax = "",
          this.email = "",
          this.postcode = "",
          this.ecotype = "",
          this.legPersonCode = "",
          this.legPerson = '',
          this.legPersonIdcode = '',
          this.defPrincipal = "",
          this.defDepartContway = "",
          this.businessScope = "",
          this.registeredCapital = "",
          this.floorSpace = '',
          this.agency = '',
          this.agencyName = '',
          this.specRecordCode = '',
          this.specRecordAgency = "",
          this.specRecordAname = '',
          this.induLicenceCode = '',
          this.induLicenceAgency = '',
          this.unisocialCreditCode = '',
          this.unisocialLicenceAgency = '',
          this.busLicenceCode = "",
          this.busLicenceAgency = '',
          this.taxLicenceCode = '',
          this.taxLicenceAgency = ''

      },
      define() {
        axios({
          method: 'post',
          url: process.env.API_HOST+'/qy/workReg',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          data: JSON.stringify({
            openDate: this.openDate,
            companyType: this.companyType,
            register: this.register,
            cname: this.cname,
            caddress: this.caddress,
            contway: this.contway,
            fax: this.fax,
            email: this.email,
            postcode: this.postcode,
            ecotype: this.ecotype,
            legPersonCode: this.legPersonCode,
            legPerson: this.legPerson,
            legPersonIdcode: this.legPersonIdcode,
            defPrincipal: this.defPrincipal,
            defDepartContway: this.defDepartContway,
            businessScope: this.businessScope,
            registeredCapital: this.registeredCapital,
            floorSpace: this.floorSpace,
            agency: this.agency,
            agencyName: this.agencyName,
            specRecordCode: this.specRecordCode,
            specRecordAgency: this.specRecordAgency,
            specRecordAname: this.specRecordAname,
            induLicenceCode: this.induLicenceCode,
            induLicenceAgency: this.induLicenceAgency,
            unisocialCreditCode: this.unisocialCreditCode,
            unisocialLicenceAgency: this.unisocialLicenceAgency,
            busLicenceCode: this.busLicenceCode,
            busLicenceAgency: this.busLicenceAgency,
            taxLicenceCode: this.taxLicenceCode,
            taxLicenceAgency: this.taxLicenceAgency
          })
        }).then((res) => {

          if (res.data.code == 200) {
            this.$message({
              message: '注册成功',
              type: 'success'
            });
            let that = this
            setTimeout(function () {
              that.$router.push("/login")
            }, 100)


          } else {
            this.$message({
              message: '注册失败',
              type: 'error'
            });
          }


        }).catch((err) => {
          this.$message({
            message: '注册失败',
            type: 'error'
          });
        })
      },
      getEcotype() {
        axios({
          method: 'post',
          url: process.env.API_HOST+ '/qy/getEcotype',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
        }).then((res) => {
          this.ecotypelist = res.data.data;
        })
      }
    },
    mounted() {
      this.getEcotype()
    },
  }

</script>
<style>
  .regist {
    overflow: hidden;
  }

  .regist .grid-content {
    background: #163258;
    color: #fff;
    height: 50px;
    line-height: 50px;
    text-align: left;
    font-size: 26px;
    padding: 0 0 0 5%;
    position: fixed;
    width: 100%;
    z-index: 999;
  }

  .regist input {
    width: 300px;
    height: 28px;
  }

  .regist table tr td:nth-child(1) {
    width: 250px;
    text-align: left;
  }

  .regist table tr td #time input.el-input__inner {
    border: 0 !important;
    width: 200px;
  }

  .regist select {
    width: 304px;
    height: 32px;
  }

</style>



