<template>
  <div>
   <div id="myChart" :style="{width: '1000px', height: '500px'}"></div>
   <div><span style="display:inline-block;width:150px;height:30px;background:#AB72B3;margin-right:15px;cursor: pointer;color:#fff" @click="week">7日业务图</span><span style="display:inline-block;width:150px;height:30px;background:#FBCB00;cursor: pointer;color:#fff" @click="weekly">15日业务图</span></div>
   </div>
</template>
<script>
export default {
  data() {
      return {
        time:['2012', '2013', '2014', '2015', '2016','2017','2018','2019','2020'],
        
      }
  },
  mounted() {
      this.drawLine()
  },
  methods: {
      weekly(){
        this.time=['2012', '2013', '2014', '2015', '2016','2017','2018','2019','2020'],
         this.drawLine()
      },
      week(){
        this.time=['2012', '2013', '2014', '2015', '2016','2017'],
         this.drawLine()
      },
      drawLine(){
           let myChart = this.$echarts.init(document.getElementById('myChart'))
            var labelOption = {
   
}
var option = {
    color: ['#A1D3D7', '#7476C9', '#ccc', '#AB72B3'],
    title:{
        text:"业务统计："
    },
    legend: {
        width:"150px",
        height:"50px",
        left:"right",
         data: ['维修', '美容', '配件', '停放']
    },
   
    calculable: true,
    xAxis: [
        {
            type: 'category',
            axisTick: {show: false},
            data:this.time
           
        }
    ],
    yAxis: [
        {
            type: 'value'
        }
    ],
    series: [
        {
            name: '维修',
            type: 'bar',
            barGap: 0,
            label: labelOption,
            data: [10, 10, 10, 10, 10]
        },
        {
            name: '美容',
            type: 'bar',
            label: labelOption,
            data: [10, 18, 19, 23, 29]
        },
        {
            name: '配件',
            type: 'bar',
            label: labelOption,
            data: [15, 23, 20, 15, 19]
        },
        {
            name: '停放',
            type: 'bar',
            label: labelOption,
            data: [98, 77, 101, 99, 40]
        }
    ]
};
          myChart.setOption(option);
      }
  },
}
</script>
<style  scope>
    
     
   

</style>



